var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',[_vm._ssrNode("<main class=\"main\"><div class=\"recruit\"><section class=\"recruit-main\"><div class=\"menu-static\"><div class=\"menu-page\"><div class=\"container\"><ul id=\"menu-theme-admissions-menu\" class=\"menu-list\"><li id=\"menu-item-494\" class=\"menu-item menu-item-type-post_type menu-item-object-page menu-item-494\"><a href=\"/dang-ky-nhap-hoc/\">Đăng ký<br>tuyển sinh</a></li> <li id=\"menu-item-729\" class=\"menu-item menu-item-type-post_type menu-item-object-page current-menu-item page_item page-item-722 current_page_item menu-item-729\"><a href=\"/cau-hoi-thuong-gap/\" aria-current=\"page\">Câu hỏi<br>thường gặp</a></li></ul></div></div></div> <section class=\"ss-faqs\"><div class=\"faqs ss-pd\"><div class=\"container\"><h2 data-aos=\"fade-down-cus\" class=\"t-title black mb-32\">\n                câu hỏi thường gặp </h2> <div class=\"faqs-list\"><div data-aos=\"fade-up-cus\" class=\"faqs-item\"><div class=\"faqs-head\"><p class=\"name\">\n                      Thời gian tôi có thể đến tham quan trực tiếp tại trường? </p> <i class=\"fas fa-chevron-right\"></i></div> <div class=\"faqs-body\"><div class=\"mona-content\"><p>Phụ huynh có thể đến thăm quan trực tiếp tại trường lúc 7h30 – 17h từ thứ 2 – sáng thứ 7 hàng\n                        tuần.</p></div></div></div> <div data-aos=\"fade-up-cus\" class=\"faqs-item\"><div class=\"faqs-head\"><p class=\"name\">\n                      Thời gian tuyển sinh </p> <i class=\"fas fa-chevron-right\"></i></div> <div class=\"faqs-body\"><div class=\"mona-content\"><ul><li>Thời gian tuyển sinh chính từ tháng 3 – tháng 8 hàng năm.</li> <li>Học sinh Mầm non có thể nhập học rải rác trong năm.</li> <li>Đối với học sinh khối Phổ thông có thể chuyển trường 2 lần trong năm học nhưng phải theo\n                          đúng thời gian quy định của Bộ GD&amp;ĐT.</li></ul></div></div></div> <div data-aos=\"fade-up-cus\" class=\"faqs-item\"><div class=\"faqs-head\"><p class=\"name\">\n                      Điều kiện nhập học</p> <i class=\"fas fa-chevron-right\"></i></div> <div class=\"faqs-body\"><div class=\"mona-content\"><ul><li>Mầm non: Bé đủ 12 tháng tuổi trở lên, không có khiếm khuyết về thể chất và hành vi là có thể\n                          nhập học.</li> <li>Phổ thông: Học sinh khi nhập học tại cần đáp ứng đủ các điều kiện sau:<ul><li>Hạnh kiểm từ Khá trở lên</li> <li>Đầy đủ học bạ (trừ lớp 1)</li> <li>Giấy giới thiệu chuyển trường (nếu là học sinh chuyển trường)</li> <li>Giấy hoàn thành cuối cấp học (nếu có)</li> <li>Đủ điều kiện Tốt nghiệp Mầm non, Tiểu học, THCS</li> <li>Hoàn thành bài Kiểm tra năng lực Tiếng Anh</li></ul></li></ul></div></div></div> <div data-aos=\"fade-up-cus\" class=\"faqs-item\"><div class=\"faqs-head\"><p class=\"name\">\n                      Khi nào học sinh được làm bài kiểm tra đầu vào? </p> <i class=\"fas fa-chevron-right\"></i></div> <div class=\"faqs-body\"><div class=\"mona-content\"><p>Khi cha mẹ học sinh quyết định cho con học tại trường, làm thủ tục nhập học và đóng\n                        khoản phí ghi danh, nhà trường sẽ sắp xếp cho học sinh làm bài kiểm tra năng lực của bé. Lứa\n                        tuổi Mầm non các con sẽ có buổi nói chuyện với giáo viên tâm lý của trường để đánh giá về các\n                        năng khiếu, thế mạnh và sở trường của các con. Từ đó, trong quá trình học, giáo viên sẽ theo dõi\n                        và hỗ trợ các con tốt hơn trong việc phát huy năng lực bản thân. </p></div></div></div> <div data-aos=\"fade-up-cus\" class=\"faqs-item\"><div class=\"faqs-head\"><p class=\"name\">\n                      Thủ tục đăng ký nhập học là như thế nào? Thủ tục chuyển trường như thế nào? </p> <i class=\"fas fa-chevron-right\"></i></div> <div class=\"faqs-body\"><div class=\"mona-content\"><ul><li>CMHS đăng ký cho bé tại trường (đóng phí trực tiếp hoặc chuyển khoản)</li> <li>Nhà trường gửi: Giấy tiếp nhận học sinh (có dấu và chữ ký của Hiệu trưởng); Đơn xin chuyển\n                          trường</li> <li>CMHS điền thông tin vào Đơn xin chuyển trường; đem Giấy tiếp nhận học sinh và Đơn xin chuyển\n                          trường đến trường cũ của con để rút học bạ</li> <li>Trường cũ sẽ bàn giao cho CMHS: Học bạ bản chính, Giấy giới thiệu chuyển trường, Bảng điểm\n                          in giấy (có đóng dấu của nhà trường).</li> <li>Hồ sơ nhập học bao gồm:<ul><li>Học bạ bản chính</li> <li>Giấy giới thiệu chuyển trường</li> <li>Bảng điểm in giấy (trường hợp HS chuyển trường trong năm)</li> <li>Đơn xin nhập học (mẫu nhà trường)</li> <li>Phiếu đăng ký đi xe đưa rước (mẫu nhà trường nếu học sinh đăng ký)</li> <li>Phiếu đăng ký thông tin người được đưa đón học sinh (mẫu nhà trường)</li></ul></li></ul></div></div></div> <div data-aos=\"fade-up-cus\" class=\"faqs-item\"><div class=\"faqs-head\"><p class=\"name\">\n                      Chương trình dã ngoại của trường các em được đi đến đâu và học những gì? </p> <i class=\"fas fa-chevron-right\"></i></div> <div class=\"faqs-body\"><div class=\"mona-content\"><ul><li>Các em được tham gia các chuyến học tập thực tế dã ngoại bên ngoài như đến các trung tâm từ\n                          thiện, vườn rau hay hoa quả trái cây, sở thú, khu di tích lịch sử địa phương, khu du lịch sinh\n                          thái, khu du lịch của các tỉnh lân cận, chuyến đi trong ngày giúp các em trải nghiệm cũng như\n                          thư giãn sau những buổi học tập trên lớp.</li> <li>Trước khi đi, thầy cô sẽ cho mục đích yêu cầu các em làm đề tài thu hoạch, báo cáo, thuyết\n                          trình sau chuyến đi đó, giúp các em tư duy vui mà học, học mà chơi.</li></ul></div></div></div> <div data-aos=\"fade-up-cus\" class=\"faqs-item\"><div class=\"faqs-head\"><p class=\"name\">\n                      Hoạt động ngoại khóa trong giờ học các em học môn nào? </p> <i class=\"fas fa-chevron-right\"></i></div> <div class=\"faqs-body\"><div class=\"mona-content\"><p>Cuối mỗi ngày là giờ học ngoại khóa các em sẽ được tham gia các câu lạc bộ của trường:</p> <ul><li>CLB nghệ thuật năng khiếu: Các em được học đàn organ, học hát, nhảy hiện đại Aerobic, vẽ,\n                          kịch,…</li> <li>CLB thể thao: Đá bóng, yoga, bơi lội,…</li></ul></div></div></div> <div data-aos=\"fade-up-cus\" class=\"faqs-item\"><div class=\"faqs-head\"><p class=\"name\">\n                      Thức ăn trường nấu hay đặt ở ngoài mang tới, có đảm bảo vệ sinh không? </p> <i class=\"fas fa-chevron-right\"></i></div> <div class=\"faqs-body\"><div class=\"mona-content\"><ul><li>Thức ăn của học sinh nhà trường nấu bếp ăn 1 chiều đủ tiêu chuẩn y tế, đầu bếp chuyên\n                          nghiệp, vệ sinh sạch sẽ đảm bảo được đầy đủ dinh dưỡng và hợp vệ sinh cho các em. Nguồn gốc\n                          thực phẩm từ các nhà cung cấp Coopmart, Vinpmart, BigC. Mẫu thức ăn trong ngày của các em sẽ\n                          được lưu trữ trong vòng 24h tại Phòng y tế của trường để theo dõi.</li> <li>Ngoài ra, đối với khối Mầm non vẫn áp dụng chương trình sữa học đường theo chương\n                          trình của Bộ Giáo dục.</li></ul></div></div></div> <div data-aos=\"fade-up-cus\" class=\"faqs-item\"><div class=\"faqs-head\"><p class=\"name\">\n                      Các con học từ mấy giờ? Có ăn sáng tại trường không? </p> <i class=\"fas fa-chevron-right\"></i></div> <div class=\"faqs-body\"><div class=\"mona-content\"><ul><li>Học bán trú 2 buổi, trường đón học sinh sáng từ 6h30 và kết thúc buổi học lúc 16h30.</li> <li>Các con ăn 3 buổi: ăn sáng, ăn trưa và ăn xế ở trường</li></ul></div></div></div> <div data-aos=\"fade-up-cus\" class=\"faqs-item\"><div class=\"faqs-head\"><p class=\"name\">\n                      Học sinh có thể mang cơm ở nhà theo để ăn trưa được không? </p> <i class=\"fas fa-chevron-right\"></i></div> <div class=\"faqs-body\"><div class=\"mona-content\"><ul><li> tuyển sinh 100% bán trú, CMHS nên khuyến khích các con ăn cùng bạn ở trường.</li> <li>Nếu trường hợp vì lý do sức khỏe đặc biệt nhà trường sẽ xem xét về khẩu phần ăn riêng cho\n                          học sinh.</li></ul></div></div></div> <div data-aos=\"fade-up-cus\" class=\"faqs-item\"><div class=\"faqs-head\"><p class=\"name\">\n                      Một tuần con học mấy ngày? </p> <i class=\"fas fa-chevron-right\"></i></div> <div class=\"faqs-body\"><div class=\"mona-content\"><ul><li>Các con học từ thứ 2 đến thứ 7 hằng tuần tùy theo địa phương.</li> <li>Buổi sáng trường đón trẻ lúc 6h30 để cho các con ăn sáng và ra về lúc 4h30 chiều mỗi ngày.\n                        </li></ul></div></div></div> <div data-aos=\"fade-up-cus\" class=\"faqs-item\"><div class=\"faqs-head\"><p class=\"name\">\n                      Làm thế nào để theo dõi con trong thời gian học tại trường? Giờ sinh hoạt của con như thế nào?\n                    </p> <i class=\"fas fa-chevron-right\"></i></div> <div class=\"faqs-body\"><div class=\"mona-content\"><ul><li>Các con tập trung tập thể dục buổi sáng và ăn sáng từ 7h – 8h.</li> <li>Bé từ dưới 3 – 12 tuổi ăn trưa lúc 10h, ngủ từ 11h đến 2h chiều còn bé 3 – 5\n                          tuổi ăn trưa từ 10h30 và ngủ từ 11h30 đến 2h chiều.</li> <li>Các con ngủ dậy vệ sinh, thay quần áo và ăn xế lúc 2h30.</li> <li>Buổi trưa các con ngủ tại lớp, có bộ mền gối ngủ trưa (bộ Kits ba mẹ mua cho con khi nhập\n                          học).</li></ul></div></div></div> <div data-aos=\"fade-up-cus\" class=\"faqs-item\"><div class=\"faqs-head\"><p class=\"name\">\n                      Đóng học phí như thế nào? </p> <i class=\"fas fa-chevron-right\"></i></div> <div class=\"faqs-body\"><div class=\"mona-content\"><ul><li>CMHS có thể đóng phí theo năm được ưu đãi 5%, hoặc đóng theo 4 lần thu phí trong năm</li> <li>Một năm học là 10 tháng chia làm 2 học kỳ, mỗi học kỳ là 5 tháng; mỗi học kỳ chia làm 2 học\n                          phần, mỗi học phần là 2.5 tháng. Như vậy 1 năm có 4 lần thu phí. </li></ul></div></div></div></div></div></div></section> <div data-bg=\"https://ischool.vn/wp-content/uploads/2022/10/HI_00015-1-scaled.jpg\" id=\"register\" class=\"dki ss-pd rocket-lazyload\"><div class=\"container\"><div class=\"dki-width\"><div id=\"wpcf7-f335-p722-o1\" lang=\"vi\" dir=\"ltr\" class=\"wpcf7 no-js\"><div class=\"screen-reader-response\"><p role=\"status\" aria-live=\"polite\" aria-atomic=\"true\"></p> <ul></ul></div> <form action=\"/cau-hoi-thuong-gap/#wpcf7-f335-p722-o1\" method=\"post\" aria-label=\"Contact form\" novalidate=\"novalidate\" data-status=\"init\" class=\"wpcf7-form init\"><div style=\"display: none;\"><input type=\"hidden\" name=\"_wpcf7\" value=\"335\"> <input type=\"hidden\" name=\"_wpcf7_version\" value=\"5.7.3\"> <input type=\"hidden\" name=\"_wpcf7_locale\" value=\"vi\"> <input type=\"hidden\" name=\"_wpcf7_unit_tag\" value=\"wpcf7-f335-p722-o1\"> <input type=\"hidden\" name=\"_wpcf7_container_post\" value=\"722\"> <input type=\"hidden\" name=\"_wpcf7_posted_data_hash\" value> <input type=\"hidden\" name=\"_wpcf7_recaptcha_response\" value></div> <span class=\"wpcf7-form-control-wrap your-source\"><input size=\"40\" aria-invalid=\"false\" value=\"Câu hỏi thường gặp\" type=\"hidden\" name=\"your-source\" class=\"wpcf7-form-control wpcf7dtx-dynamictext wpcf7-dynamichidden\"></span> <div class=\"head dki-head t-center mb-56\"><h2 data-aos=\"flip-right\" class=\"t-title white mb-24\">\n                      Đăng ký tư vấn\n                    </h2> <p data-aos=\"fade-up-cus\" class=\"text c-white\">\n                      Để nhận được hỗ trợ chi tiết nhất, Quý Phụ huynh vui lòng để lại thông tin bên dưới\n\n                    </p></div> <div data-aos=\"fade-up-cus\" class=\"dki-form\"><div class=\"f-gr mb-24\"><div class=\"f-list\"><div class=\"f-item f-item-2\"><span id=\"wpcf7-641bed015d477-wrapper\" class=\"wpcf7-form-control-wrap yourfullname-wrap\" style=\"display:none !important; visibility:hidden !important;\"><label for=\"wpcf7-641bed015d477